/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
    background-color: rgb(18, 18, 59);
    height: 100%;
    width: 100%;
}

header {
    display: flex;
    justify-content: space-between;
    background-color: black;
    color: seashell;
    font-family: Arial, Helvetica, sans-serif;
    font-style: italic;
    height: 40px;
    
}

h1 {
    margin-top: 15px;
    margin-left: 20px;
}


.modal-button {
    margin-top: 14px;
    margin-right: 25px;
    font-size: 18px;
    cursor: pointer;
}

.modal-container {
    position: fixed;
    color: seashell;
    background-color: black;
    font-family: Arial, Helvetica, sans-serif;
    top: 25%;
    left: 38%;
    width: 320px;
    height: 120px;
    max-width: 80%;
    border-radius: 5%;
    z-index: 10;
    transform: scale(0);
}

.modal-container.live {
    transform: scale(1);
}

.modal-header {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    padding: 7px;
    border-bottom: 1px solid  rgb(18, 18, 70);
}

.modal-body {
    padding: 10px;
    font-size: 14px;
}

.modal-header button {
    font-size: 15px;
    font-weight: bold;
    color: seashell;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
}

.modal-header div {
    padding-left: 4px;
    padding-top: 3px;
}

 .icons {
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: 100px;
    height: 25px;
    top: 84%;
    left: 45%;
    // margin-top: 550px;
    // margin-left: 500px;
}

.trash {
    position: relative;
    float: right;
    margin-top: 5px;
    margin-right: 20px;
    color: darkgrey;
    font-size: 15px;
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -325px;
    width: 650px;
    height: 200px;
    background-color: rgb(18, 18, 70);
    border: solid;
    border-radius: 1%;
    border-color:  rgb(18, 18, 70);
    // opacity: 0.4;
}

.play {
    position: absolute;
    background-color: darkgrey;
    width: 12.5px;
    height: 12.5px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    margin: 2%;
    margin-top: .75%;
    clip-path: polygon(
        0 0,
        50% 25%,
        50% 75%,
        50% 75%,
        50% 25%,
        100% 50%,
        100% 50%,
        0 100%
    );
}

.pause {    
    position: absolute;
    background-color: darkgrey;
    width: 12.5px;
    height: 12.5px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    margin: 2%;
    margin-top: .75%;
    clip-path: polygon(
        0 0,
        40% 0,
        40% 100%,
        60% 100%,
        60%  0,
        100% 0,
        100% 100%,
        0 100%
    );
}

.playpause-container {
    position: relative;
    margin-left: 1%;

}
.row {
    height: 25px;
    padding: 5px;
}

.row .box {
    float: left;
    border: solid;
    border-color: grey;
    border-width: 1px;
    width: 25px;
    height: inherit;
    background-color: grey;
    margin: 3px;
    border-radius: 5%;
    opacity: 0.3;
}

.box:hover {
    background-color: lightgray;
}

.row .box.selected {
    background-color: lightgray;
}

.row .instrument {
    float: left;
    border: solid;
    border-color: grey;
    border-width: 1px;
    width: 100px;
    height: inherit;
    background-color: grey;
    margin: 3px;
    text-align: center;
    border-radius: 2%;
    opacity: 0.5;
}

.instrument p {
    padding: 4px;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: lavender;
}

.instrument .sym {
    font-size: 10px;
    padding-left: 7px;
    padding-top: 8px;
}

.instrument .sound {
    padding-right: 30px;
}

.instrument .sound.tom {
    padding-right: 47px;
}

.instrument .sound.snare {
    padding-right: 48px;
}

.instrument .sound.kick {
    padding-right: 48px;
}


.instrument {
    display: flex;
    justify-content: space-between;
    // align-items: center;
}

.instrument:hover {
    background-color: darkgray;
}

.btn {
    display: flexbox;
    position: relative;
    margin: 10px;
    
}

